<template>
	<div>
		<img v-if="isImage()" :src="attachment.url" />
		<div v-else class="attachment">
			<a :href="attachment.url">{{attachment.name}}</a>
			<span>{{attachment.size}} bytes</span>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Attachment',
	props: ['attachment'],
	data: function() {
		return {}
	},
	methods: {
		isImage () {
			const fileExtension = this.attachment.url.split('.').slice(-1)[0].toLowerCase();

			console.log(fileExtension, this.attachment);

			return (
				fileExtension === 'png' ||
				fileExtension === 'jpeg' ||
				fileExtension === 'jpg' ||
				fileExtension === 'gif'
			);
		}
	}
}
</script>