<template>
	<div v-bind:class="{'flex-col': true, collapsed: !visible}" id="find-panel">
		<div id="find-controls" class="flex-row">
			<div>
				<label>
					<input type="checkbox" v-model="filterByText">
					<span> Contains text: </span>
				</label>
				<input type="text" v-model="textFilter">
			</div>
			<div>
				
				<label>
					<input type="checkbox" v-model="filterByUser">
					<span> From user (name or ID): </span>
				</label>
				<input type="text" v-model="userFilter">
			</div>
			<div>
				<label>
					<input type="checkbox" v-model="filterByChannel">
					<span> From channel: </span>
				</label>
				<select v-model="channelFilter">
					<option v-for="channel in server.channels.values()" v-bind:value="channel.id" v-bind:key="channel.id">
						{{channel.name}}
					</option>
				</select>
			</div>
			<div>
				<button v-on:click="searchMessages">Find</button>
			</div>
		</div>
		<message-list v-bind:messages="foundMessages"></message-list>
		<div id="find-toggle" v-on:click="visible = !visible">🔍</div>
	</div>
</template>

<script>
import {mapState} from 'vuex';

import MessageList from './MessageList';

export default {
	name: 'FindPanel',
	props: [],
	data: () => {
		return {
				filterByText: false,
				filterByUser: false,
				filterByChannel: false,
				textFilter: '',
				userFilter: '',
				channelFilter: '',
				visible: true,
				foundMessages: []
			}
	},
	methods: {
		searchMessages () {
			const MAX_MESSAGES = 10000;
			let allMessages = [];
			const channels = this.server.channels;
			for (const [id, channel] of channels) {
				if (
					channel.type !== 'text' ||
					(this.filterByChannel && this.channelFilter !== channel.id)
				) continue;

				let messages = channel.messages;

				if (this.filterByText) {
					const matchText = this.textFilter.toLowerCase();
					messages = messages.filter(message => message.content.toLowerCase().includes(matchText));
				}

				const members = this.server.members;
				if (this.filterByUser) {
					messages = messages.filter(message => message.author === this.userFilter ||
						members[message.author].username === this.userFilter
					);
				}

				allMessages = allMessages.concat(messages);
			}

			if (allMessages.length > MAX_MESSAGES) {
				alert(`More than ${MAX_MESSAGES} messages found. Not displaying results.`);
				return;
			}

			this.foundMessages = allMessages.sort((a, b) => a.createdTimestamp - b.createdTimestamp);
		},
	},
	computed: {
		...mapState(['server'])
	},

	components: {MessageList}
}
</script>